<template>
  <div class="min-h-screen bg-gray-50 py-8">
    <div class="max-w-4xl mx-auto px-4 sm:px-6 lg:px-8">
      <!-- Header -->
      <div class="text-center mb-8">
        <h1 class="text-4xl font-bold text-gray-900 mb-4">
          🤖 AI Анализ резюме
        </h1>
        <p class="text-xl text-gray-600 max-w-2xl mx-auto mb-4">
          Загрузите ваше резюме и получите детальный анализ от искусственного интеллекта. 
          Узнайте, как улучшить ваше резюме и увеличить шансы на успех.
        </p>
        
      </div>

      <!-- Upload Section -->
      <div v-if="!analysisResult" class="bg-white rounded-2xl shadow-lg p-8 mb-8">
        <div class="text-center">
          <div class="mb-6">
            <div class="mx-auto w-24 h-24 bg-blue-100 rounded-full flex items-center justify-center mb-4">
              <span class="text-4xl">📄</span>
            </div>
            <h2 class="text-2xl font-semibold text-gray-900 mb-2">
              Загрузите ваше резюме
            </h2>
            <p class="text-gray-600">
              Поддерживаются форматы: PDF, DOC, DOCX (максимум 10 МБ)
            </p>
          </div>

          <!-- File Upload Area -->
          <div 
            @drop="handleDrop"
            @dragover.prevent
            @dragenter.prevent
            class="border-2 border-dashed border-gray-300 rounded-xl p-8 mb-6 transition-colors"
            :class="{ 'border-blue-500 bg-blue-50': isDragOver }"
          >
            <div v-if="!selectedFile" class="text-center">
              <div class="mx-auto w-16 h-16 bg-gray-100 rounded-full flex items-center justify-center mb-4">
                <span class="text-2xl">📁</span>
              </div>
              <p class="text-lg text-gray-600 mb-4">
                Перетащите файл сюда или
              </p>
              <label 
                for="file-upload" 
                class="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-medium cursor-pointer transition-colors"
              >
                Выберите файл
              </label>
              <input
                id="file-upload"
                type="file"
                accept=".pdf,.doc,.docx"
                @change="handleFileSelect"
                class="hidden"
              />
            </div>

            <!-- Selected File -->
            <div v-else class="text-center">
              <div class="mx-auto w-16 h-16 bg-green-100 rounded-full flex items-center justify-center mb-4">
                <span class="text-2xl">✅</span>
              </div>
              <p class="text-lg font-medium text-gray-900 mb-2">
                {{ selectedFile.name }}
              </p>
              <p class="text-sm text-gray-600 mb-4">
                {{ formatFileSize(selectedFile.size) }}
              </p>
              <button
                @click="removeFile"
                class="text-red-600 hover:text-red-700 text-sm font-medium"
              >
                Удалить файл
              </button>
            </div>
          </div>

          <!-- Analysis Options -->
          <div class="bg-gray-50 rounded-xl p-6 mb-6">
            <h3 class="text-lg font-semibold text-gray-900 mb-4">
              Настройки анализа
            </h3>
            <div class="grid md:grid-cols-2 gap-4">
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-2">
                  Целевая позиция
                </label>
                <input
                  v-model="analysisOptions.targetPosition"
                  type="text"
                  placeholder="Например: Frontend Developer"
                  class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
                />
              </div>
              <div>
                <label class="block text-sm font-medium text-gray-700 mb-2">
                  Опыт работы
                </label>
                <select
                  v-model="analysisOptions.experienceLevel"
                  class="w-full px-4 py-2 border border-gray-300 rounded-lg focus:ring-blue-500 focus:border-blue-500"
                >
                  <option value="">Выберите уровень</option>
                  <option value="junior">Junior (0-2 года)</option>
                  <option value="middle">Middle (2-5 лет)</option>
                  <option value="senior">Senior (5+ лет)</option>
                </select>
              </div>
            </div>
          </div>

          <!-- Analyze Button -->
          <button
            @click="analyzeResume"
            :disabled="!selectedFile || isAnalyzing"
            class="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed text-white px-8 py-4 rounded-lg font-semibold text-lg transition-colors"
          >
            <span v-if="isAnalyzing" class="flex items-center justify-center">
              <svg class="animate-spin -ml-1 mr-3 h-5 w-5 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle class="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" stroke-width="4"></circle>
                <path class="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              Анализируем резюме...
            </span>
            <span v-else>
              🚀 Начать анализ
            </span>
          </button>
        </div>
      </div>

      <!-- Analysis Results -->
      <div v-if="analysisResult" class="space-y-6">
        <!-- Back Button -->
        <div class="flex justify-start">
          <button
            @click="resetAnalysis"
            class="flex items-center text-blue-600 hover:text-blue-700 font-medium"
          >
            <span class="mr-2">←</span>
            Загрузить другое резюме
          </button>
        </div>

        <!-- Overall Score -->
        <div class="bg-white rounded-2xl shadow-lg p-8">
          <div class="text-center mb-6">
            <h2 class="text-2xl font-bold text-gray-900 mb-2">
              Общая оценка резюме
            </h2>
            <div class="flex items-center justify-center mb-4">
              <div class="relative w-32 h-32">
                <svg class="w-32 h-32 transform -rotate-90" viewBox="0 0 120 120">
                  <circle
                    cx="60"
                    cy="60"
                    r="50"
                    stroke="#e5e7eb"
                    stroke-width="8"
                    fill="none"
                  />
                  <circle
                    cx="60"
                    cy="60"
                    r="50"
                    stroke="#3b82f6"
                    stroke-width="8"
                    fill="none"
                    stroke-dasharray="314"
                    :stroke-dashoffset="314 - (314 * analysisResult.overallScore) / 100"
                    class="transition-all duration-1000"
                  />
                </svg>
                <div class="absolute inset-0 flex items-center justify-center">
                  <span class="text-3xl font-bold text-gray-900">
                    {{ analysisResult.overallScore }}%
                  </span>
                </div>
              </div>
            </div>
            <p class="text-lg text-gray-600">
              {{ getScoreDescription(analysisResult.overallScore) }}
            </p>
          </div>
        </div>

        <!-- Detailed Analysis -->
        <div class="grid md:grid-cols-2 gap-6">
          <!-- Strengths -->
          <div class="bg-white rounded-2xl shadow-lg p-6">
            <h3 class="text-xl font-semibold text-green-600 mb-4 flex items-center">
              <span class="mr-2">✅</span>
              Сильные стороны
            </h3>
            <ul class="space-y-3">
              <li 
                v-for="strength in analysisResult.strengths" 
                :key="strength"
                class="flex items-start"
              >
                <span class="text-green-500 mr-2 mt-1">•</span>
                <span class="text-gray-700">{{ strength }}</span>
              </li>
            </ul>
          </div>

          <!-- Areas for Improvement -->
          <div class="bg-white rounded-2xl shadow-lg p-6">
            <h3 class="text-xl font-semibold text-orange-600 mb-4 flex items-center">
              <span class="mr-2">⚠️</span>
              Области для улучшения
            </h3>
            <ul class="space-y-3">
              <li 
                v-for="improvement in analysisResult.improvements" 
                :key="improvement"
                class="flex items-start"
              >
                <span class="text-orange-500 mr-2 mt-1">•</span>
                <span class="text-gray-700">{{ improvement }}</span>
              </li>
            </ul>
          </div>
        </div>

        <!-- ATS Score and Keywords -->
        <div class="grid md:grid-cols-2 gap-6">
          <!-- ATS Score -->
          <div class="bg-white rounded-2xl shadow-lg p-6">
            <h3 class="text-xl font-semibold text-blue-600 mb-4 flex items-center">
              <span class="mr-2">🤖</span>
              ATS Совместимость
            </h3>
            <div class="text-center mb-4">
              <div class="text-4xl font-bold text-blue-600 mb-2">
                {{ analysisResult.atsScore }}%
              </div>
              <p class="text-gray-600">
                Вероятность прохождения автоматической фильтрации
              </p>
            </div>
            <div class="w-full bg-gray-200 rounded-full h-3">
              <div 
                class="bg-blue-600 h-3 rounded-full transition-all duration-1000"
                :style="{ width: analysisResult.atsScore + '%' }"
              ></div>
            </div>
          </div>

          <!-- Keywords Analysis -->
          <div class="bg-white rounded-2xl shadow-lg p-6">
            <h3 class="text-xl font-semibold text-purple-600 mb-4 flex items-center">
              <span class="mr-2">🔍</span>
              Анализ ключевых слов
            </h3>
            
            <!-- Present Keywords -->
            <div class="mb-4">
              <h4 class="font-semibold text-green-600 mb-2">Найденные ключевые слова:</h4>
              <div class="flex flex-wrap gap-2">
                <span 
                  v-for="keyword in analysisResult.keywords.present" 
                  :key="keyword"
                  class="bg-green-100 text-green-800 px-3 py-1 rounded-full text-sm"
                >
                  {{ keyword }}
                </span>
              </div>
            </div>

            <!-- Missing Keywords -->
            <div v-if="analysisResult.keywords.missing.length > 0">
              <h4 class="font-semibold text-red-600 mb-2">Рекомендуемые ключевые слова:</h4>
              <div class="flex flex-wrap gap-2">
                <span 
                  v-for="keyword in analysisResult.keywords.missing" 
                  :key="keyword"
                  class="bg-red-100 text-red-800 px-3 py-1 rounded-full text-sm"
                >
                  {{ keyword }}
                </span>
              </div>
            </div>
          </div>
        </div>

        <!-- Detailed Sections -->
        <div class="space-y-6">
          <div 
            v-for="section in analysisResult.sections" 
            :key="section.name"
            class="bg-white rounded-2xl shadow-lg p-6"
          >
            <h3 class="text-xl font-semibold text-gray-900 mb-4 flex items-center">
              <span class="mr-2">{{ section.icon }}</span>
              {{ section.name }}
              <span class="ml-auto text-sm font-normal text-gray-500">
                {{ section.score }}/10
              </span>
            </h3>
            <div class="w-full bg-gray-200 rounded-full h-2 mb-4">
              <div 
                class="bg-blue-600 h-2 rounded-full transition-all duration-1000"
                :style="{ width: (section.score * 10) + '%' }"
              ></div>
            </div>
            <p class="text-gray-700 mb-4">{{ section.description }}</p>
            <div v-if="section.recommendations && section.recommendations.length > 0">
              <h4 class="font-semibold text-gray-900 mb-2">Рекомендации:</h4>
              <ul class="space-y-2">
                <li 
                  v-for="rec in section.recommendations" 
                  :key="rec"
                  class="flex items-start"
                >
                  <span class="text-blue-500 mr-2 mt-1">→</span>
                  <span class="text-gray-700">{{ rec }}</span>
                </li>
              </ul>
            </div>
          </div>
        </div>

        <!-- Response Rate Prediction -->
        <div class="bg-white rounded-2xl shadow-lg p-6">
          <h3 class="text-xl font-semibold text-gray-900 mb-4 flex items-center">
            <span class="mr-2">📊</span>
            Прогноз откликов
          </h3>
          <div class="text-center">
            <div class="text-5xl font-bold text-green-600 mb-2">
              {{ analysisResult.estimatedResponseRate }}%
            </div>
            <p class="text-gray-600 mb-4">
              Ожидаемый процент положительных откликов от работодателей
            </p>
            <div class="w-full bg-gray-200 rounded-full h-4 mb-4">
              <div 
                class="bg-green-600 h-4 rounded-full transition-all duration-1000"
                :style="{ width: analysisResult.estimatedResponseRate + '%' }"
              ></div>
            </div>
            <p class="text-sm text-gray-500">
              * Прогноз основан на анализе структуры резюме и ключевых слов
            </p>
          </div>
        </div>

        <!-- Action Buttons -->
        <div class="bg-white rounded-2xl shadow-lg p-6">
          <h3 class="text-xl font-semibold text-gray-900 mb-4">
            Следующие шаги
          </h3>
          <div class="grid md:grid-cols-3 gap-4">
            <button class="bg-blue-600 hover:bg-blue-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
              📝 Скачать улучшенное резюме
            </button>
            <button class="bg-green-600 hover:bg-green-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
              💼 Найти подходящие вакансии
            </button>
            <button class="bg-purple-600 hover:bg-purple-700 text-white px-6 py-3 rounded-lg font-medium transition-colors">
              🎯 Персональные рекомендации
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted } from 'vue';
import { resumeAnalysisService, type ResumeAnalysisOptions, type ResumeAnalysisResult } from '@/services/resumeAnalysis';

// Reactive data
const selectedFile = ref<File | null>(null);
const isDragOver = ref(false);
const isAnalyzing = ref(false);
const analysisResult = ref<ResumeAnalysisResult | null>(null);

const analysisOptions = reactive<ResumeAnalysisOptions>({
  targetPosition: '',
  experienceLevel: ''
});


// File handling
const handleFileSelect = (event: Event) => {
  const target = event.target as HTMLInputElement;
  if (target.files && target.files[0]) {
    selectedFile.value = target.files[0];
  }
};

const handleDrop = (event: DragEvent) => {
  event.preventDefault();
  isDragOver.value = false;
  
  if (event.dataTransfer && event.dataTransfer.files[0]) {
    selectedFile.value = event.dataTransfer.files[0];
  }
};

const removeFile = () => {
  selectedFile.value = null;
};

const formatFileSize = (bytes: number): string => {
  if (bytes === 0) return '0 Bytes';
  const k = 1024;
  const sizes = ['Bytes', 'KB', 'MB', 'GB'];
  const i = Math.floor(Math.log(bytes) / Math.log(k));
  return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
};

// Analysis functions
const analyzeResume = async () => {
  if (!selectedFile.value) return;
  
  isAnalyzing.value = true;
  
  try {
    // Analyze resume using AI service with direct file upload
    analysisResult.value = await resumeAnalysisService.analyzeResumeWithFile(
      selectedFile.value,
      analysisOptions
    );
  } catch (error) {
    console.error('Ошибка при анализе резюме:', error);
    alert(`Ошибка при анализе резюме: ${error instanceof Error ? error.message : 'Неизвестная ошибка'}`);
  } finally {
    isAnalyzing.value = false;
  }
};

const resetAnalysis = () => {
  analysisResult.value = null;
  selectedFile.value = null;
  analysisOptions.targetPosition = '';
  analysisOptions.experienceLevel = '';
};

const getScoreDescription = (score: number): string => {
  if (score >= 90) return 'Отличное резюме! Готово к отправке.';
  if (score >= 80) return 'Хорошее резюме с небольшими улучшениями.';
  if (score >= 70) return 'Неплохое резюме, но есть что улучшить.';
  if (score >= 60) return 'Резюме требует значительных доработок.';
  return 'Резюме нуждается в серьезной переработке.';
};

// Component mounted
onMounted(() => {
  console.log('🚀 Resume Analysis page loaded');
});

// Drag and drop event listeners
document.addEventListener('dragover', (e) => {
  e.preventDefault();
  isDragOver.value = true;
});

document.addEventListener('dragleave', (e) => {
  e.preventDefault();
  isDragOver.value = false;
});
</script>

<style scoped>
/* Custom scrollbar */
::-webkit-scrollbar {
  width: 6px;
}

::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb {
  background: #c1c1c1;
  border-radius: 3px;
}

::-webkit-scrollbar-thumb:hover {
  background: #a8a8a8;
}
</style>
