// Prompts for DeepSeek AI resume analysis

export interface ResumeAnalysisPrompt {
  system: string;
  user: string;
}

/**
 * Main prompt for comprehensive resume analysis
 */
export const createResumeAnalysisPrompt = (
  resumeText: string,
  targetPosition?: string,
  experienceLevel?: string
): ResumeAnalysisPrompt => {
  const systemPrompt = `Ты - эксперт по HR и карьерному консультированию с 10+ летним опытом. 
Твоя задача - провести детальный анализ резюме и предоставить конструктивные рекомендации.

АНАЛИЗИРУЙ РЕЗЮМЕ ПО СЛЕДУЮЩИМ КРИТЕРИЯМ:
1. Структура и оформление
2. Контактная информация
3. Опыт работы и достижения
4. Образование и сертификаты
5. Навыки (технические и soft skills)
6. Ключевые слова и ATS-совместимость
7. Общее впечатление

ВАЖНО: Отвечай ТОЛЬКО чистым JSON без markdown блоков, без дополнительного текста, без объяснений.

Формат ответа:
{
  "overallScore": число от 0 до 100,
  "strengths": ["сильная сторона 1", "сильная сторона 2", ...],
  "improvements": ["рекомендация 1", "рекомендация 2", ...],
  "sections": [
    {
      "name": "Название раздела",
      "icon": "эмодзи",
      "score": число от 0 до 10,
      "description": "описание оценки",
      "recommendations": ["рекомендация 1", "рекомендация 2"]
    }
  ],
  "keywords": {
    "present": ["найденное ключевое слово 1", "найденное ключевое слово 2"],
    "missing": ["рекомендуемое ключевое слово 1", "рекомендуемое ключевое слово 2"]
  },
  "atsScore": число от 0 до 100,
  "estimatedResponseRate": число от 5 до 100
}`;

  const userPrompt = `Проанализируй следующее резюме${targetPosition ? ` для позиции "${targetPosition}"` : ''}${experienceLevel ? ` (уровень: ${experienceLevel})` : ''}:

${resumeText}

ВАЖНО: 
- Будь конструктивным и конкретным в рекомендациях
- Учитывай современные требования рынка труда
- Оценивай ATS-совместимость резюме
- Предлагай конкретные улучшения
- Отвечай ТОЛЬКО чистым JSON без markdown, без дополнительного текста`;

  return {
    system: systemPrompt,
    user: userPrompt
  };
};

/**
 * Prompt for keyword analysis
 */
export const createKeywordAnalysisPrompt = (
  resumeText: string,
  targetPosition: string
): ResumeAnalysisPrompt => {
  const systemPrompt = `Ты - эксперт по SEO и ATS-системам. 
Проанализируй резюме на предмет ключевых слов, соответствующих целевой позиции.

ОТВЕЧАЙ В JSON ФОРМАТЕ:
{
  "present": ["найденное ключевое слово 1", "найденное ключевое слово 2"],
  "missing": ["рекомендуемое ключевое слово 1", "рекомендуемое ключевое слово 2"],
  "atsCompatibility": число от 0 до 100
}`;

  const userPrompt = `Проанализируй ключевые слова в резюме для позиции "${targetPosition}":

${resumeText}

Найди:
1. Ключевые слова, которые уже есть в резюме
2. Важные ключевые слова, которых не хватает
3. Оцени ATS-совместимость (0-100)

Отвечай только в JSON формате.`;

  return {
    system: systemPrompt,
    user: userPrompt
  };
};

/**
 * Prompt for improvement suggestions
 */
export const createImprovementPrompt = (
  resumeText: string,
  targetPosition: string
): ResumeAnalysisPrompt => {
  const systemPrompt = `Ты - карьерный консультант. 
Предоставь конкретные рекомендации по улучшению резюме.

ОТВЕЧАЙ В JSON ФОРМАТЕ:
{
  "improvements": [
    {
      "category": "категория улучшения",
      "suggestion": "конкретная рекомендация",
      "priority": "высокий/средний/низкий"
    }
  ],
  "estimatedImprovement": "ожидаемое улучшение в процентах"
}`;

  const userPrompt = `Предоставь рекомендации по улучшению резюме для позиции "${targetPosition}":

${resumeText}

Сфокусируйся на:
- Конкретных улучшениях
- Приоритизации рекомендаций
- Ожидаемом эффекте от изменений

Отвечай только в JSON формате.`;

  return {
    system: systemPrompt,
    user: userPrompt
  };
};

/**
 * Prompt for ATS optimization
 */
export const createATSOptimizationPrompt = (
  resumeText: string
): ResumeAnalysisPrompt => {
  const systemPrompt = `Ты - эксперт по ATS (Applicant Tracking Systems).
Оцени резюме на совместимость с автоматическими системами отбора.

ОТВЕЧАЙ В JSON ФОРМАТЕ:
{
  "atsScore": число от 0 до 100,
  "issues": ["проблема 1", "проблема 2"],
  "recommendations": ["рекомендация 1", "рекомендация 2"],
  "compatibility": "отличная/хорошая/удовлетворительная/плохая"
}`;

  const userPrompt = `Оцени ATS-совместимость резюме:

${resumeText}

Проверь:
- Структуру и форматирование
- Ключевые слова
- Разделы и заголовки
- Формат файла и читаемость

Отвечай только в JSON формате.`;

  return {
    system: systemPrompt,
    user: userPrompt
  };
};

/**
 * Prompt for response rate prediction
 */
export const createResponseRatePrompt = (
  resumeText: string,
  targetPosition: string
): ResumeAnalysisPrompt => {
  const systemPrompt = `Ты - HR-аналитик с опытом работы с большими данными.
Предскажи вероятность получения положительного отклика от работодателей.

ОТВЕЧАЙ В JSON ФОРМАТЕ:
{
  "responseRate": число от 5 до 25,
  "factors": {
    "positive": ["положительный фактор 1", "положительный фактор 2"],
    "negative": ["отрицательный фактор 1", "отрицательный фактор 2"]
  },
  "confidence": "высокая/средняя/низкая"
}`;

  const userPrompt = `Предскажи процент положительных откликов для резюме на позицию "${targetPosition}":

${resumeText}

Учитывай:
- Соответствие требованиям позиции
- Качество оформления
- Опыт и достижения
- Рыночные тренды

Отвечай только в JSON формате.`;

  return {
    system: systemPrompt,
    user: userPrompt
  };
};
